/* MESH LOADER */
@keyframes mesh {
  0% {
    transform-origin: 50% -100%;
    transform: rotate(0);
  }
  50% {
    transform-origin: 50% -100%;
    transform: rotate(360deg);
  }
  50.1% {
    transform-origin: 50% 200%;
    transform: rotate(0deg);
  }
  100% {
    transform-origin: 50% 200%;
    transform: rotate(360deg);
  }
}
@keyframes rotate-animation {
  0% {
    transform: rotate3d(1, 1, 1, 0deg);
  }
  25% {
    transform: rotate3d(1, 1, 1, 2deg);
  }
  50% {
    transform: rotate3d(1, 1, 1, 0deg);
  }
  75% {
    transform: rotate3d(1, 1, 1, -2deg);
  }
  100% {
    transform: rotate3d(1, 1, 1, 0deg);
  }
}
.mesh-loader {
  overflow: hidden;
  height: inherit;
  width: inherit;
}
.mesh-loader .circle {
  position: absolute;
  background: #006666;
  border-radius: 50%;
  margin: -3px;
  animation: mesh 3s ease-in-out infinite -1.5s;
  width: 6px;
  height: 6px;
}
.mesh-loader > div .circle:last-child {
  animation-delay: 0s;
}
.mesh-loader > div {
  position: absolute;
  top: 50%;
  left: 50%;
}
.mesh-loader > div:last-child {
  transform: rotate(90deg);
}

@-webkit-keyframes animloader14 {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
@keyframes animloader14 {
  0% {
    transform: scale(0);
    opacity: 1;
  }
